<template>
  <div v-if="block" class="alert alert-danger alert-dismissible fade show" role="alert">
    <button type="button" class="close" data-dismiss="alert" aria-label="Close">
      <span aria-hidden="true">&times;</span>
    </button>
    <h4 class="alert-heading"> AdBlock </h4>
    <p> AdBlock  AdBlock </p>
    <hr />
    <p class="mb-0"></p>
  </div>
</template>

<script>
export default {
  name: "AdBlockWarning",
  data() {
    return {
      block: false,
    };
  },
  mounted() {
    let vm = this;
    setTimeout(function () {
      if (typeof window.google_jobrunner === "undefined") {
        vm.block = true;
      } else {
        vm.block = false;
      }
    }, 1000);
  },
};
</script>
